import { embedPluginFactory } from '@ali/4ever-factory';
import toolbar from './toolbar';
import createRenderNode from './createRenderNode';
import * as actions from './actions';

export { actions };

const createVideoPlugin = embedPluginFactory({
  createRenderNode,
  // @ts-ignore
  toolbar,
  configName: 'video',
  actions,
}, () => ({
  key: 'video'
}));

export default createVideoPlugin;
